import React, {Component} from 'react';
import './Sidebar.scss';
import avatar from '../../../assets/images/avatar.png';
import NavItem from './NavItem';

class Topbar extends Component {
  constructor(props) {
    super(props)
    this.state = {
      navList: [
        {name: '', path: '/', icon: 'index'},
        {name: '', path: '/category', icon: 'category'},
        {name: '', path: '/tags', icon: 'tag'},
        {name: '', path: '/archives', icon: 'archive'},
        {name: '', path: '/about', icon: 'about'}
      ]
    }
  }
  
  makeNavItem () {
    const { navList } = this.state;
    return navList.map(item => <NavItem key={item.name} name={item.name} icon={item.icon} path={item.path} />)
  }
  render () {
    return <div className="side-bar p-t-40">
      <div data-flex="dir:top cross:center">
        <img src={avatar} className="avatar" alt="avatar"/>

        <nav className="p-t-30" data-flex="dir:top cross:center">
          {this.makeNavItem()}
        </nav>
      </div>
    </div>
  }
}

export default Topbar;



